.work_page h1 {
  text-align: center;
}

.description {
  padding: 5px;
}

.work_page button {
  font-size: small;
  min-width: 130px;
}

.back {
  width: 48px;
  height: 70px;
  background-image: url(../../img/arrow_back_white_48dp.svg);
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-self: flex-end;
  float: left;
}

.work_page ul {
  height: 70px;
  list-style: none;
  margin-left: auto;
  display: flex;
  align-self: flex-end;
  float: right;
}

.work_page li {
  margin: 10px;
}

.custom_radio {
  width: 48px;
  height: 48px;
  display: inline-block;
  cursor: pointer;
}

