@media (min-width: 700px) {
  .work_page {
    padding: 10px;
  }

  .work_page button {
    font-size: 18px;
    min-width: 180px;
  }
}

@media (min-width: 1000px) {
  .container{
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 10px;
  }
}